* {
  box-sizing: border-box;
}

html, body {
  height: 100vh;
  width: 100vw;
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.1em;
  overflow-x: hidden;
  overflow-y: auto;
}

body {
  -webkit-user-select: none;
  user-select: none;
  background-color: #f5f5f9;
}

.chat-container {
  height: 500px;
  margin: 0;
  padding: 0;
  overflow: auto;
  height: 100% !important;
}

.chat-message {
  background-color: #fff;
  border-radius: 10px;
  margin: 0 0 1.1rem;
  padding: 19px 5px 7px;
  position: relative;
}

.message-buttons {
  float: right;
  border: none;
  border-radius: 4px;
  margin: 0;
  padding: 0;
  position: relative;
  top: -19px;
  right: 5px;
}

.message-button {
  cursor: pointer;
  border: none;
  border-radius: 4px;
  margin: 1px;
  padding: 0 2px;
  position: relative;
}

.message-button:hover {
  top: 2px;
  left: 2px;
}

.markdown-editor {
  margin: 0;
  padding: 0;
  display: flex;
}

.markdown-editor .textarea-container {
  float: left;
  margin: 0;
  padding: 0;
  position: relative;
}

.markdown-editor textarea {
  width: 95vw;
  max-width: 95vw;
  font-size: 100%;
}

.markdown-editor .editor-buttons {
  z-index: 14;
  position: absolute;
  top: 0;
  right: 5px;
}

.chat-message-msg {
  -webkit-user-select: text;
  user-select: text;
  margin: 0;
  padding: 0;
}

.chat-message-msg p {
  white-space: pre-wrap;
  margin: 1px;
  padding: 1px;
  font-size: .9rem;
}

.chat-message-msg p code {
  background-color: #f5f5f9;
  border-radius: 2.5px;
  padding: 1.4px 1.2px;
  font-size: 82%;
}

.inline-container {
  width: 60%;
  height: 50px;
  z-index: 10;
  background-color: #0000;
  margin: 4px;
  padding: 0;
  position: absolute;
  pointer-events: all !important;
}

#terminal {
  width: 80%;
  height: 50px;
  z-index: 10;
  background-color: #0000;
  margin: 4px;
  padding: 0;
  position: absolute;
  pointer-events: all !important;
}

.output-container {
  border-top: 1.5px solid #000;
  margin-top: 10px;
  padding-top: 2px;
  display: none;
}

.output-container:before {
  font-variant: small-caps;
  content: attr(x-output-tag);
  font-family: monospace;
}

.chat-message-msg pre {
  max-width: 95vw;
  white-space: pre-wrap;
  background-color: #f5f5f9;
  border-radius: 2px;
  margin-top: 7px;
  margin-bottom: 10px;
  margin-left: 10px;
  font-size: .89rem;
}

.hljs {
  padding: .5em !important;
}

.code-buttons {
  float: right;
  min-height: 40px;
  height: 40px;
  border: none;
  border-radius: 4px;
  margin: 0;
  padding: 0;
  position: relative;
  top: 5px;
  right: 5px;
}

.code-button {
  cursor: pointer;
  border: none;
  border-radius: 4px;
  margin: 1px;
  padding: 0 2px;
  position: relative;
}

.code-button:hover {
  top: 2px;
  left: 2px;
}

@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
    display: block;
  }
}

@keyframes fadeout {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
    display: none !important;
  }
}

.notification {
  float: inline-end;
  white-space: nowrap;
  background-color: #fdfdfd;
  border: .9px solid #d9d9d9;
  border-radius: 3px;
  margin: 0;
  padding: 2px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: .5rem;
  line-height: .5rem;
  animation: 1s fadein;
  display: block;
  position: absolute;
  top: 2.8em;
  right: 0;
}

.chat-message-self {
  background-color: #dcf8c6;
}

.chat-message-username {
  -webkit-user-select: text;
  user-select: text;
  color: silver;
  font-size: 12px;
  position: absolute;
  top: 0;
  bottom: -20px;
  left: 2px;
}

.chat-message-time {
  color: #999;
  font-size: 12px;
  position: absolute;
  bottom: -10px;
  right: 0;
}

